import React, { useState, useEffect } from 'react'
import noteService from './services/notes'
import loginService from './services/login'
import LoginForm from './components/loginPage'
import Togglable from './components/toggle'
import NoteForm from './components/noteForm'
import './App.css'

const Note = ({ note, toggleImportance, deleteNote }) => {
  const label = note.important ? 'make not important' : 'make important'
  const createdBy = JSON.stringify(note.user.name)
  // console.log('note?', note)
  // console.log('createdBy', createdBy)
  return (
    <li>
      Important? {note.important.toString()}::: {note.content}
      <button onClick={toggleImportance}>{label}</button>
      <button value={note.id} onClick={() => deleteNote(note)}>
        Delete
      </button>
      <br />
      Created by: {createdBy}
    </li>
  )
}

const Notification = ({ errorMessage, text, status }) => {
  if (errorMessage === null) {
    return null
  }

  return (
    <div text={text} status={status} className="error">
      {errorMessage.text}
    </div>
  )
}

const App = () => {
  const [notes, setNotes] = useState([])
  const [newNote, setNewNote] = useState('')
  const [showAll, setShowAll] = useState(true)
  const [errorMessage, setErrorMessage] = useState(null)
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [user, setUser] = useState(null)
  const [loginVisible, setLoginVisible] = useState(false)

  useEffect(() => {
    noteService.getAll().then((initialNotes) => {
      setNotes(initialNotes)
    })
  }, [])

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedNoteappUser')
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      setUser(user)
      noteService.setToken(user.token)
    }
  }, [])

  const handleMessage = (data) => {
    setErrorMessage(data)
    setTimeout(() => {
      setErrorMessage(null)
    }, 3000)
  }

  const notesToShow = showAll
    ? notes
    : notes.filter((note) => note.important === true)

  const addNote = (event) => {
    event.preventDefault()

    const noteObject = {
      content: newNote,
      user: { username: user.username, name: user.name },
      date: new Date().toISOString(),
      important: Math.random() < 0.5,
    }

    noteService
      .create(noteObject)
      .then((returnedNote) => {
        setNotes(notes.concat(returnedNote))
        setNewNote('')
      })
      .catch((error) => {
        handleMessage({ text: error.response.data.error })
      })
  }

  const toggleImportanceOf = (id) => {
    const note = notes.find((n) => n.id === id)

    const noteAuthor = note.user.name
    const currentUser = user.name
    const changedNote = { ...note, important: !note.important }
    if (noteAuthor !== currentUser) {
      setNotes(notesToShow)
      handleMessage({
        text: ` '${currentUser}' is not authorized to change this note `,
      })
      return
    } else {
      noteService
        .update(id, changedNote)
        .then(() => {
          setNotes(
            notesToShow.map((note) => (note.id !== id ? note : changedNote))
          )
          handleMessage({
            text: `the importance of note '${
              note.id
            }' has been changed to ${changedNote.important.toString()} `,
          })
        })
        .catch(() => {
          setErrorMessage({
            text: `the note '${note.content}' was already deleted from server`,
          })
          setTimeout(() => {
            setErrorMessage(null)
          }, 5000)
          setNotes(notes.filter((n) => n.id !== id))
        })
    }
  }

  const handleNoteChange = (event) => {
    setNewNote(event.target.value)
  }

  const deleteNote = (note) => {
    let index = notes.indexOf(note)

    const noteUser = note.user.name
    const currentUser = user.name

    if (noteUser === currentUser) {
      noteService.deleteNote(note).then(() => {
        setErrorMessage({ text: `Note id: ${note.id} deleted`, status: 'ok' })
        setTimeout(() => {
          setErrorMessage(null)
        }, 3000)
      })
      notes.splice(index, 1)
      setNotes([...notes])
    }
    if (noteUser !== currentUser) {
      setErrorMessage({ text: `${currentUser} not authorized`, status: 'ok' })
      setTimeout(() => {
        setErrorMessage(null)
      }, 3000)
    } else if (user === null) {
      setErrorMessage({ text: `Who are you?`, status: 'ok' })
      setTimeout(() => {
        setErrorMessage(null)
      }, 3000)
    }
  }

  const handleLogin = async (event) => {
    event.preventDefault()
    try {
      const user = await loginService.login({
        username,
        password,
      })
      window.localStorage.setItem('loggedNoteappUser', JSON.stringify(user))
      noteService.setToken(user.token)
      setUser(user)
      setUsername('')
      setPassword('')
    } catch (exception) {
      handleMessage({ text: exception.response.data.error })
    }
  }

  const handleLogout = async (event) => {
    event.preventDefault()
    window.localStorage.removeItem('loggedNoteappUser')
    setUser(null)
  }

  const loginForm = () => {
    return (
      <div>
        <Togglable buttonLabel="login">
          <LoginForm
            username={username}
            password={password}
            handleUsernameChange={({ target }) => setUsername(target.value)}
            handlePasswordChange={({ target }) => setPassword(target.value)}
            handleSubmit={handleLogin}
          />
        </Togglable>
      </div>
    )
  }

  const noteForm = () => (
    <Togglable buttonLabel="new note">
      <NoteForm
        onSubmit={addNote}
        value={newNote}
        handleChange={handleNoteChange}
      />
    </Togglable>
  )

  /*   const title = () => {
    if (showAll === true) {
      return <h1>Notes: All Notes</h1>
    } else {
      return <h1>Notes: Important Notes</h1>
    }
  }
  console.log(title) */
  return (
    <div>
      {/*       {title()} */}
      <h1> NOTES APP</h1>

      <Notification errorMessage={errorMessage} />
      {user === null ? (
        loginForm()
      ) : (
        <div>
          <p>
            {user.name} is logged in{' '}
            <button onClick={handleLogout}>logout</button>
          </p>
          {noteForm()}
        </div>
      )}
      <div>
        <button onClick={() => setShowAll(!showAll)}>
          show {showAll ? 'important' : 'all'}
        </button>
      </div>
      <ul>
        {notesToShow.map((note) => (
          <Note
            key={note.id}
            note={note}
            user={note.user}
            toggleImportance={() => toggleImportanceOf(note.id)}
            deleteNote={deleteNote}
          />
        ))}
      </ul>
    </div>
  )
}

export default App
